import React, { useEffect, useRef, useState } from "react";
import { OverlayPanel } from 'primereact/overlaypanel';
import axios from "axios";

import { InputTextarea } from 'primereact/inputtextarea';

import { YourMessage } from "./Reusable/YourMessage";
import { ResponseMessage } from "./Reusable/ResponseMessage";
import { ResponseLoading } from "./Reusable/ResponseLoading";

import "./ChatBot.css"

const SAMPLE_DATA = [
  // {
  //   id: '839283dhsjdhsjdhu2',
  //   type: 'your_message',
  //   text: 'This is your message'
  // },
  // {
  //   id: 'dhs6473gry463hg',
  //   type: 'response',
  //   text: 'This is the response message'
  // }
]

export const ChatBot = () => {
  const op = useRef(null);
  const [query, setQuery] = useState('')
  const [messages, setMessages] = useState(SAMPLE_DATA)

  const [count, setCount] = useState(0)

  useEffect(() => {
    // Load previous messages

    setMessages(
      messages.map(message => {
        if (message.type === 'your_message')
          return <YourMessage text={message.text} key={message.id} />

        if (message.type === 'response')
          return <ResponseMessage text={message.text} key={message.id} />
      })
    )
  }, [])

  function sendQuery() {
    if (query === '') return
    messages.push(<YourMessage text={query} key={Math.random()} />)
    // messages.push(<ResponseLoading />)  // Needs fix

    if (count === 0) {
      messages.push(<ResponseMessage text={'Yeah sure. Ask me anything.'} key={Math.random()} />)
      setCount(count + 1)
    } else {
      messages.push(<ResponseLoading />)
      makeAPIRequest()
    }


    newMessageArrived()
    setQuery('')
  }

  function makeAPIRequest() {
    let data = {
      query: query,
      headers: {
        "content-type": "application/json"
      }
    };

    axios
      .post('http://localhost:5000/auth/query', data)
      .then((response) => {
        console.log(response)
      })
      .catch((err) => {
        console.error(err.response.status, err.response.data, err.response.statusText)
        console.error('Failure. A valid request was made by the client but the server failed to complete the request.')
        console.error(err.response)
      });
  }

  function newMessageArrived() {
    // Not working as expected
    // const messageBoxEl = document.getElementById('message-box')
    // messageBoxEl.scrollTo(0, messageBoxEl.scrollHeight)
  }

  function toggleMessageBox(e) {
    op.current.toggle(e)
  }

  return (
    <div className="chatbot-outer-container">
      <div className="chatbot-outer-container">
        <OverlayPanel ref={op} className="chat-box">
          <div className="chatbot-container">
            <h4 className="chatbot-heading">Ask me anything</h4>

            {
              messages && messages.length > 0 ?
                (
                  <div className="messages" id="message-box">
                    {messages}
                  </div>
                ) :
                <div className="no-messages">Start asking questions...</div>
            }

            <InputTextarea
              className="query-box"
              value={query}
              onChange={e => setQuery(e.target.value)}
              rows={1}
              placeholder="Type here..."
            />
            <i
              className="pi pi-send send-icon"
              style={{ fontSize: '1.5rem' }}
              onClick={sendQuery}
            ></i>
          </div>
        </OverlayPanel>
        <div className="need-help-button" onClick={(e) => toggleMessageBox(e)} >Need help?</div>
      </div>
    </div>
  );
};
